.advantages { 
    max-width: 1850px;
    margin: 0 auto;
    position: relative;
    padding-bottom: 40px;
    margin-bottom: 97px;
    .template-title {
        h2 {
            padding-top: 90px;
            font-size: 36px;
        }
        &:after {
            background-color: $color-primary;
        }

    }
}

.advantage {
    text-align: center;
    margin-bottom: 48px;
    &-img-box {
        width: 113px;
        height: 113px;
        border: 1px solid #6C7188;
        border-radius: 50%;
        padding: 10px;
        margin: 0 auto 22px;
        &:hover{
            border: 1px solid $color-primary;
        }

        img {
            background: #89C249;
            width: 100%;
            height: auto;
            border-radius: 50%;
            object-fit: scale-down;
        }
    }
    &-title {
        font-size: 18px;
        font-weight: 400;
        max-width: 160px;
        margin: 0 auto;
    }
}