footer {
    .wrapper-footer {
        margin-top: 56px;
        margin-bottom: 50px;
    }
    .footer-logo {
        max-width: 171px;
        height: auto;
        margin-bottom: 28px;
    }
    .information-column, .column-link-wrapper {
        margin-top: 40px;
    }
    .decoration-line::before {
        content: "";
        position: absolute;
        left: 50%;
        transform: translate(-50%, 0);
        max-width: 1140px;
        width: calc(100% - 40px);
        height: 2px;
        background: $color-primary;
    }
    .row {
        --bs-gutter-x: 0;

    }
    .column-links-box{ 
        h3 {
            font-size: 20px;
            font-weight: 700;
            line-height: 146%; /* 29.2px */
            }
        a {
            font-size: 16px;
            line-height: 160.5%; 
            text-decoration: underline;
            color: #85899C;
            &:hover {
            color: $color-primary;
            }
        }
    }

    a {
        text-decoration: none;
        cursor: pointer;
        color: white;
    }
}
.column-link-wrapper { 
    max-width: 710px;
    margin-left: auto;
    .column-links-box {
        width: 33%;
        display: flex;
        flex-direction: column;
        a {
            margin: 7px 0;
        }
    }
}

 