@media screen and (max-width: 991px) {
    // header
    .container-xxl {
        padding: 0;

    }
    
    .container {
        padding-right: 20px;
        padding-left: 20px;

    }

    a.navbar-brand {
        width: 126px;

        img {
            max-width: 100%;
            height: auto;
            padding-bottom: 18px;
            padding-top: 3px;
        }
    }

    header {
        .navbar {
            padding-top: 15px;
            padding-bottom: 0;
        }

        .btn {
            padding: 23px 85px;
            display: flex;
            align-items: center;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 60px;
        }
    }
    
    .navbar-collapse {
        background: #363A4D;
        box-shadow: 14px 22px 40px 0px rgba(30, 35, 66, 0.28);
        margin-left: -20px;
        margin-right: -20px;
    }
    .nav-link {
        font-size: 24px;
        line-height: 212%;
        padding: 0;
        margin-bottom: 10px;
        text-align: center;
        font-weight: 400;
    }

    .navbar-nav {
        padding: 37px 0 22px;
    }

// first-screen
section.calc {
    margin-top: 55px;
}

    .first-screen-slider {
        
        .container {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            z-index: 2;
            padding-bottom: 50px;
            .mb-first-screen {
                margin-bottom: 72px;
            }
        }
        
        .slick-slide {
            height: calc(100vh - 72px );
            display: flex;
            align-items: center;
            background-position: center;
            &::before {
                content: "";
                position: absolute;
                z-index: 1;
                background: #0000000e;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
            }
            
        }
        .first-screen-title {
            font-size: 32px;
            margin-bottom: 10px;
            width: 297px;
        }
        .first-screen-subtitle {
            font-size: 16px;
            margin-bottom: 18px;
            width: 275px;

            
        }
        
    }
    //form
    .pos-checkbox {
    padding-top: 8px;
    margin-bottom: 6px;
}
    .container .calc-form {
        margin-left: auto;
        margin-right: auto;
    }
    .calc-decoration {
    display: none;
    }

    .calc-form {
        width: 335px;
        height: auto;
        padding: 50px 30px 30px 30px;
        
    }
    .btn-calc {
        padding: 19.4px 34px;
        font-size: 16px;
        width: 100%;
    }
    .template-title.col-lg-7 h2 {
        font-size: 24px;
        line-height: 124.5%;
        text-transform: uppercase;
        margin-bottom: 6px;
        text-align: center;
        &:after {
               position: absolute;
               top: 90%;
               left: 50%;
               transform: translate(-50%, 0%);
           }
        
    }
    .calc-form {
    margin-bottom: 60px;
}

.section-background::before {
    left: 0;
    right: 0;
    height: 100%;
    border-radius: 0;
    
}

    //advantages
    .advantages{
        margin-bottom: 54px;
        .template-title h2 {
            padding-top: 55px;
            font-size: 24px;   
        }
        .advantage-title {
            text-align: center;
            font-size: 18px;
            font-weight: 400;
            max-width: 180px;
            margin: 0 auto;
            line-height: 124.5%;
        }
    }
//delivery

.delivery {
    .template-title h2 {
        font-size: 24px;
    }
    margin: 0;
    .card {
        margin: 0 auto;
        max-width:335px;
        .slick-list {
            margin: 0 -15px 32px;
        }
    }
    .card-content-box .card-button-row{
        .btn {
            width: 138px;
            justify-content: center;
            padding: 13px 5px;
        }
    } 
    ul.slick-dots {
        margin-top: 32px;
        // margin-bottom: 60px;
        padding-bottom: 60px;
    }
} 
//services
.services {
    border-radius: 0;
    padding-top: 54px;
    padding-bottom: 0;
}
// about
.about-company .template-title h2 {
    font-size: 24px;
}
.about-company .template-title:after {
    left: 50%;
}

.about-company {
    .template-title {
        text-align: center;
    }
    padding: 54px 0 24px;
    p {
        text-align: justify;
        font-size: 16px;
        font-style: normal;
        font-weight: 300;
        line-height: 149%;
        span {
            font-weight: 400;
        }
    }
    .img-box {
        margin-top: 25px;
        
        img{
            width: 275px;
            height: auto;
            margin: 0 auto;
            display: block;
        } 
    }
}
//questions 
.questions {
    padding-bottom: 60px;
}
.questions .col-lg-8 {
    margin-bottom: 15px;
}
.questions-btn {
    padding: 19px 34px;
    font-size: 16px;
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.questions .form-questions {
    padding: 38px 20px;
    max-width: 390px;
    background-color: #363A4D;
    border-radius: 10px;
}
.questions .form-questions .form-title {

    text-align: center;
    font-weight: 400;
}

//cooperation
    .cooperation {
        padding-top: 54px;
        padding-bottom: 60px;
        text-align: justify;
        .template-title {
            h2 {
                font-size: 24px;
                font-weight: 700;
                line-height: 124.5%; /* 29.88px */
            }
            h4 {
                font-size: 16px;
                font-weight: 400;
                line-height: 124.5%; /* 29.88px */
            }
        }
    }
    .content-box {
        display: flex;
        flex-direction: column;
        ul {
            order: 2;
        }
        img {
            max-width: 335px;
            order: 1;
            height: auto;
        }
        .btn-coop {
            display: flex;
            justify-content: space-between;
            order: 3;
            width: 100%;
            padding-right: 19px;
            .icon {
                margin: 0;
                width: 27px;
                height: 27px;
                transition: 0.3s;
            }
        
        }
    }
    //footer
    .information-column {
        margin: 0 auto;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .column-link-wrapper {
        display: none;
    }
}

